@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@200;300;400;500;600;700&display=swap");

* {
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
}

body {
  background: #f8fbf1;
  padding: 20px;
}

@media only screen and (max-width: 600px) {
  .body {
    background: #f8fbf1;
    padding: 20px;
    width: 100%;
  }
}
